
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Card from 'vux/src/components/card/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: "goodsInfo",
components: {
ViewBox,
XHeader,
Card,
XButton
},
created() {
this.queryProduct();
},
data() {
return {
productInfo: {}
};
},
methods: {
goGetGoods() {
this.$router.push("/getGoods");
},
queryUser() {
this.$api
.queryUser({})
.then(data => {
if (data.code === 200) {
this.userInfo = data.data.user
} else {
this.$vux.toast.text(data.message, "top");
}
});
},
queryProduct() {
this.$api.queryProduct().then(data => {
if (data.code === 200) {
this.productInfo = data.data.product;
} else {
this.$vux.toast.text(data.message, "top");
}
});
}
}
};



